class=\"container-fluid\">\r\n                <div class=\"row\">\r\n                    <div class=\"col-md-8\">\r\n                        <div class=\"card\">\r\n                            <div class=\"card-header\">\r\n                                <h3 class=\"card-title\">\r\n                                    {{ isEdit ? 'Editar' : 'Nuevo' }} Piloto\r\n                                </h3>\r\n                            </div>\r\n                            \r\n                            <form @submit.prevent=\"submitForm\" class=\"card-body\">\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"first_name\">Nombre *</label>\r\n                                            <input \r\n                                                v-model=\"form.first_name\"\r\n                                                type=\"text\" \r\n                                                id=\"first_name\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.first_name }\"\r\n                                                required\r\n                                            >\r\n                                            <div v-if=\"errors.first_name\" class=\"invalid-feedback\">\r\n                                                {{ errors.first_name[0] }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n                                    \r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"last_name\">Apellidos *</label>\r\n                                            <input \r\n                                                v-model=\"form.last_name\"\r\n                                                type=\"text\" \r\n                                                id=\"last_name\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.last_name }\"\r\n                                                required\r\n                                            >\r\n                                            <div v-if=\"errors.last_name\" class=\"invalid-feedback\">\r\n                                                {{ errors.last_name[0] }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"rut\">RUT *</label>\r\n                                            <input \r\n                                                v-model=\"form.rut\"\r\n                                                type=\"text\" \r\n                                                id=\"rut\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.rut || rutError }\"\r\n                                                required\r\n                                                @blur=\"validateRut\"\r\n                                                placeholder=\"Ej: 12.345.678-9\"\r\n                                            >\r\n                                            <div v-if=\"errors.rut\" class=\"invalid-feedback\">\r\n                                                {{ errors.rut[0] }}\r\n                                            </div>\r\n                                            <div v-else-if=\"rutError\" class=\"invalid-feedback\">\r\n                                                {{ rutError }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n\r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"age\">Edad</label>\r\n                                            <input \r\n                                                v-model=\"form.age\"\r\n                                                type=\"number\" \r\n                                                id=\"age\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.age }\"\r\n                                            >\r\n                                            <div v-if=\"errors.age\" class=\"invalid-feedback\">\r\n                                                {{ errors.age[0] }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"status\">Estado</label>\r\n                                            <select \r\n                                                v-model=\"form.status\"\r\n                                                id=\"status\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.status }\"\r\n                                            >\r\n                                                <option value=\"active\">Activo</option>\r\n                                                <option value=\"inactive\">Inactivo</option>\r\n                                                <option value=\"suspended\">Suspendido</option>\r\n                                            </select>\r\n                                            <div v-if=\"errors.status\" class=\"invalid-feedback\">\r\n                                                {{ errors.status[0] }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n\r\n                                    <div class=\"col-md-6\">\r\n                                        <div class=\"form-group\">\r\n                                            <label for=\"club_id\">Club</label>\r\n                                            <select \r\n                                                v-model=\"form.club_id\"\r\n                                                id=\"club_id\"\r\n                                                class=\"form-control\"\r\n                                                :class=\"{ 'is-invalid': errors.club_id }\"\r\n                                            >\r\n                                                <option value=\"\">Seleccionar club...</option>\r\n                                                <option v-for=\"club in clubs\" :key=\"club.id\" :value=\"club.id\">\r\n                                                    {{ club.name }}\r\n                                                </option>\r\n                                            </select>\r\n                                            <div v-if=\"errors.club_id\" class=\"invalid-feedback\">\r\n                                                {{ errors.club_id[0] }}\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"photo\">Foto del Piloto</label>\r\n                                    <input \r\n                                        @change=\"handleFileUpload\"\r\n                                        type=\"file\" \r\n                                        id=\"photo\"\r\n                                        class=\"form-control-file\"\r\n                                        accept=\"image/*\"\r\n                                    >\r\n                                    <small class=\"form-text text-muted\">\r\n                                        Formatos permitidos: JPG, PNG, GIF. Tamaño máximo: 2MB\r\n                                    </small>\r\n                                </div>\r\n\r\n                                <div class=\"form-group\">\r\n                                    <div class=\"d-flex justify-content-between\">\r\n                                        <router-link to=\"/pilotos\" class=\"btn btn-secondary\">\r\n                                            <i class=\"fas fa-arrow-left mr-1\"></i>\r\n                                            Volver\r\n                                        </router-link>\r\n                                        \r\n                                        <button \r\n                                            type=\"submit\" \r\n                                            class=\"btn btn-primary\"\r\n                                            :disabled=\"loading\"\r\n                                        >\r\n                                            <i v-if=\"loading\" class=\"fas fa-spinner fa-spin mr-1\"></i>\r\n                                            <i v-else class=\"fas fa-save mr-1\"></i>\r\n                                            {{ isEdit ? 'Actualizar' : 'Crear' }} Piloto\r\n                                        </button>\r\n                                    </div>\r\n                                </div>\r\n                            </form>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <!-- Preview -->\r\n                    <div class=\"col-md-4\">\r\n                        <div class=\"card\">\r\n                            <div class=\"card-header\">\r\n                                <h3 class=\"card-title\">Vista Previa</h3>\r\n                            </div>\r\n                            <div class=\"card-body text-center\">\r\n                                <div class=\"mb-3\">\r\n                                    <img \r\n                                        v-if=\"form.photo_preview\" \r\n                                        :src=\"form.photo_preview\" \r\n                                        alt=\"Vista previa\"\r\n                                        class=\"img-fluid rounded-circle\"\r\n                                        style=\"width: 120px; height: 120px; object-fit: cover;\"\r\n                                    >\r\n                                    <div \r\n                                        v-else\r\n                                        class=\"bg-secondary rounded-circle d-inline-flex align-items-center justify-content-center\" \r\n                                        style=\"width: 120px; height: 120px;\"\r\n                                    >\r\n                                        <i class=\"fas fa-user fa-3x text-white\"></i>\r\n                                    </div>\r\n                                </div>\r\n                                \r\n                                <h4>{{ form.first_name || 'Nombre' }} {{ form.last_name || 'Apellidos' }}</h4>\r\n                                <p class=\"text-muted\">Edad: {{ form.age || 'No especificada' }}</p>\r\n                                \r\n                                <span :class=\"getStatusClass(form.status)\">\r\n                                    {{ getStatusLabel(form.status) }}\r\n                                </span>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </section>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: 'PilotForm',\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            clubs: [],\r\n            form: {\r\n                first_name: '',\r\n                last_name: '',\r\n                rut: '',\r\n                age: '',\r\n                status: 'active',\r\n                club_id: '',\r\n                photo: null,\r\n                photo_preview: null\r\n            },\r\n            errors: {},\r\n            rutError: ''\r\n        };\r\n    },\r\n    computed: {\r\n        isEdit() {\r\n            return !!this.$route.params.id;\r\n        },\r\n        pilotId() {\r\n            return this.$route.params.id;\r\n        }\r\n    },\r\n    async mounted() {\r\n        await this.loadFormData();\r\n        if (this.isEdit) {\r\n            await this.loadPilot();\r\n        }\r\n    },\r\n    methods: {\r\n        async loadFormData() {\r\n            try {\r\n                const [clubsResponse] = await Promise.all([\r\n                    fetch('/api/clubs')\r\n                ]);\r\n\r\n                if (clubsResponse.ok) {\r\n                    const clubsData = await clubsResponse.json();\r\n                    this.clubs = clubsData.data || clubsData;\r\n                } \r\n            } catch (error) {\r\n                console.error('Error loading form data:', error);\r\n            }\r\n        },\r\n\r\n        async loadPilot() {\r\n            try {\r\n                this.loading = true;\r\n                const response = await fetch(`/api/pilots/${this.pilotId}`);\r\n                \r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    const pilot = data.data || data;\r\n                    \r\n                    this.form = {\r\n                        first_name: pilot.first_name || '',\r\n                        last_name: pilot.last_name || '',\r\n                        age: pilot.age || '',\r\n                        status: pilot.status || 'active',\r\n                        club_id: pilot.club_id || '',\r\n                        photo: null,\r\n                        photo_preview: pilot.photo ? `/storage/${pilot.photo}` : null\r\n                    };\r\n                } else {\r\n                    this.$router.push('/pilotos');\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading pilot:', error);\r\n                this.$router.push('/pilotos');\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        validateRut() {\r\n            if (!this.form.rut) {\r\n                this.rutError = 'El RUT es obligatorio';\r\n                return false;\r\n            }\r\n            if (!this.isValidRut(this.form.rut)) {\r\n                this.rutError = 'El RUT ingresado no es válido';\r\n                return false;\r\n            }\r\n            this.rutError = '';\r\n            return true;\r\n        },\r\n        isValidRut(rut) {\r\n            // Limpia formato\r\n            rut = rut.replace(/[^0-9kK]/g, '').toUpperCase();\r\n            if (rut.length < 8) return false;\r\n            let cuerpo = rut.slice(0, -1);\r\n            let dv = rut.slice(-1);\r\n            let suma = 0, multiplo = 2;\r\n            for (let i = cuerpo.length - 1; i >= 0; i--) {\r\n                suma += parseInt(cuerpo.charAt(i)) * multiplo;\r\n                multiplo = multiplo < 7 ? multiplo + 1 : 2;\r\n            }\r\n            let dvEsperado = 11 - (suma % 11);\r\n            dvEsperado = dvEsperado === 11 ? '0' : dvEsperado === 10 ? 'K' : dvEsperado.toString();\r\n            return dv === dvEsperado;\r\n        },\r\n\r\n        async submitForm() {\r\n            this.loading = true;\r\n            this.errors = {};\r\n            if (!this.validateRut()) {\r\n                this.loading = false;\r\n                return;\r\n            }\r\n\r\n            try {\r\n                const formData = new FormData();\r\n                \r\n                Object.keys(this.form).forEach(key => {\r\n                    if (key !== 'photo_preview' && this.form[key] !== null && this.form[key] !== '') {\r\n                        formData.append(key, this.form[key]);\r\n                    }\r\n                });\r\n\r\n                const url = this.isEdit ? `/api/pilots/${this.pilotId}` : '/api/pilots';\r\n                const method = this.isEdit ? 'PUT' : 'POST';\r\n\r\n                // Para PUT con FormData, necesitamos usar POST con _method\r\n                if (this.isEdit) {\r\n                    formData.append('_method', 'PUT');\r\n                }\r\n\r\n                const response = await fetch(url, {\r\n                    method: this.isEdit ? 'POST' : 'POST',\r\n                    headers: {\r\n                        'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n                    },\r\n                    body: formData\r\n                });\r\n\r\n                const data = await response.json();\r\n\r\n                if (response.ok) {\r\n                    console.log('PilotForm: Submit successful, isEdit:', this.isEdit);\r\n                    if (this.isEdit) {\r\n                        // Si es edición, regresar al detalle del piloto con parámetro de éxito\r\n                        const redirectUrl = `/pilotos/${this.pilotId}?updated=true`;\r\n                        console.log('PilotForm: Redirecting to:', redirectUrl);\r\n                        this.$router.push(redirectUrl);\r\n                    } else {\r\n                        // Si es creación, ir a la lista de pilotos\r\n                        console.log('PilotForm: Redirecting to pilots list');\r\n                        this.$router.push('/pilotos');\r\n                        this.showSuccessMessage('Piloto creado exitosamente');\r\n                    }\r\n                } else {\r\n                    if (data.errors) {\r\n                        this.errors = data.errors;\r\n                    }\r\n                }\r\n            } catch (error) {\r\n                console.error('Error submitting form:', error);\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        handleFileUpload(event) {\r\n            const file = event.target.files[0];\r\n            if (file) {\r\n                this.form.photo = file;\r\n                \r\n                // Crear preview\r\n                const reader = new FileReader();\r\n                reader.onload = (e) => {\r\n                    this.form.photo_preview = e.target.result;\r\n                };\r\n                reader.readAsDataURL(file);\r\n            }\r\n        },\r\n\r\n        getStatusClass(status) {\r\n            const classes = {\r\n                'active': 'badge badge-success',\r\n                'inactive': 'badge badge-secondary',\r\n                'suspended': 'badge badge-danger'\r\n            };\r\n            return classes[status] || 'badge badge-secondary';\r\n        },\r\n\r\n        getStatusLabel(status) {\r\n            const labels = {\r\n                'active': 'Activo',\r\n                'inactive': 'Inactivo',\r\n                'suspended': 'Suspendido'\r\n            };\r\n            return labels[status] || 'Desconocido';\r\n        },\r\n\r\n        showSuccessMessage(message) {\r\n            // Crear notificación temporal\r\n            const notification = document.createElement('div');\r\n            notification.className = 'alert alert-success alert-dismissible fade show position-fixed';\r\n            notification.style.cssText = 'top: 20px; right: 20px; z-index: 9999; min-width: 300px;';\r\n            notification.innerHTML = `\r\n                <i class=\"fas fa-check-circle mr-2\"></i>\r\n                ${message}\r\n                <button type=\"button\" class=\"close\" data-dismiss=\"alert\">\r\n                    <span>&times;</span>\r\n                </button>\r\n            `;\r\n            \r\n            document.body.appendChild(notification);\r\n            \r\n            // Auto-remover después de 4 segundos\r\n            setTimeout(() => {\r\n                if (notification.parentNode) {\r\n                    notification.parentNode.removeChild(notification);\r\n                }\r\n            }, 4000);\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.pilot-form {\r\n    padding: 0;\r\n}\r\n\r\n.content-header {\r\n    padding: 15px 0;\r\n}\r\n\r\n.card {\r\n    border: none;\r\n    box-shadow: 0 0 1px rgba(0,0,0,.125), 0 1px 3px rgba(0,0,0,.2);\r\n}\r\n\r\n.form-group label {\r\n    font-weight: 600;\r\n}\r\n\r\n.is-invalid {\r\n    border-color: #dc3545;\r\n}\r\n\r\n.invalid-feedback {\r\n    display: block;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
